
import React from 'react'
import {Layout, Menu, Dropdown, Icon} from 'antd'
import { Link } from 'react-router-dom'
import BookItem from './bookItem'
import styles from '../styles/main.less'



const { Header, Content } = Layout

class AppComponent extends React.Component {
  constructor(props) {
    super(props)
    this.bookList = []
    this.menu = (
      <Menu>
        <Menu.Item key="0">
          <a href="http://www.alipay.com/"></a>
        </Menu.Item>
        <Menu.Item key="1">
          <a href="http://www.taobao.com/"><Icon type="setting"/></a>
        </Menu.Item>
        <Menu.Item key="2">
          <a href="http://www.tmall.com/"><Icon type="question-circle-o"/></a>
        </Menu.Item>
      </Menu>
    )
  }
 
  componentWillMount() {
    this.bookList.push(<BookItem/>)
    this.bookList.push(<BookItem/>)
    this.bookList.push(<BookItem/>)
    this.bookList.push(<BookItem/>)
    this.bookList.push(<BookItem/>)
    this.bookList.push(<BookItem/>)
    this.bookList.push(<BookItem/>)
    this.bookList.push(<BookItem/>)
    this.bookList.push(<BookItem/>)
    this.bookList.push(<BookItem/>)
    this.bookList.push(<BookItem/>)
    this.bookList.push(<BookItem/>)
    this.bookList.push(<BookItem/>)
    this.bookList.push(<BookItem/>)
  }

  render() {
    return (
      <div>
        <Layout>
          <Header className={styles.header}>
            <span className={styles.title}>oho</span>
            <Dropdown
              overlay={this.menu}
              placement="bottomRight"
              trigger={['click']}
              >
              <img src="../images/menu.png" className={styles.dropdown}/>
            </Dropdown>
            <Link to="/search"><Icon type="search" className={styles.search}/></Link>
          </Header>
          <Content className={styles.content}>
            {this.bookList}
          </Content>
        </Layout>
      </div>
    )
  }
}

AppComponent.defaultProps = {
}

export default AppComponent



